  font-size: 12px;
  line-height: 1.4;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

@media screen and (min-width: 768px) {
  .product-modal__label {
    font-size: 14px;
  }
}

.product-modal__label-comment {
  color: var(--additional-color);
  font-size: 12px;
  line-height: 1.4;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 12%;
  left: 12px;
}

@media screen and (min-width: 768px) {
  .product-modal__label-comment {
    font-size: 14px;
  }
}

.product-modal__input {
  width: 224px;
  height: 32px;
